
*{margin:0px;padding:0px;outline:none
}
body,html,#app{width:100%;height:100%;position:relative
}
input,body,button{font-family:"Open Sans", sans-serif
}
@keyframes bouncy{
0%{transform:translateY(0%)
}
33%{transform:translateY(-100%)
}
66%{transform:translateY(100%)
}
}
@keyframes bounceIn{
from,20%,40%,60%,80%,to{animation-timing-function:cubic-bezier(0.215, 0.61, 0.355, 1)
}
0%{opacity:0;transform:scale3d(0.3, 0.3, 0.3)
}
20%{transform:scale3d(1.1, 1.1, 1.1)
}
40%{transform:scale3d(0.9, 0.9, 0.9)
}
60%{opacity:1;transform:scale3d(1.03, 1.03, 1.03)
}
80%{transform:scale3d(0.97, 0.97, 0.97)
}
to{opacity:1;transform:scale3d(1, 1, 1)
}
}
@keyframes bounceInLeft{
from,60%,75%,90%,to{animation-timing-function:cubic-bezier(0.215, 0.61, 0.355, 1)
}
0%{opacity:0;transform:translate3d(-3000px, 0, 0)
}
60%{opacity:1;transform:translate3d(25px, 0, 0)
}
75%{transform:translate3d(-10px, 0, 0)
}
90%{transform:translate3d(5px, 0, 0)
}
to{transform:none
}
}
.fade-leave-active{transition:opacity 1s
}
.fade-enter,.fade-leave-active{opacity:0
}
.appear-enter-active,.appear-leave-active{transition:opacity .5s
}
.appear-enter,.appear-leave-active{opacity:0
}
.global-loader{position:fixed;top:0px;left:0px;width:100%;height:100%;background-color:rgba(255,255,255,0.9);z-index:5000
}
.global-loader .dots{display:block;font-size:0;color:#333;width:54px;height:18px;position:absolute;top:50%;left:50%;margin-left:-27px;margin-top:-9px
}
.global-loader .dots>div{display:inline-block;width:10px;height:10px;margin:4px;border-radius:100%;animation:bouncy .6s infinite ease-in-out
}
.global-loader .dots>div:nth-child(1){background-color:#0ee1e3;animation-delay:-.14s
}
.global-loader .dots>div:nth-child(2){background-color:#0bd6eb;animation-delay:-.07s
}
.global-loader .dots>div:nth-child(3){background-color:#00b4ff;animation-delay:0s
}
.global-application-error{position:fixed;top:0px;left:0px;width:100%;height:100%;background-color:rgba(255,26,26,0.19);z-index:5000
}
.global-application-error .error{background-color:white;position:absolute;top:50%;left:50%;width:300px;margin-left:-150px;padding:20px;border-radius:6px;text-align:center
}
.global-application-error .error p{margin-bottom:20px
}
.global-application-error .error button{border:2px solid #d0d0d0;height:34px;border-radius:4px;padding:0px 20px;background-color:white
}
.global-application-error .error button:hover{cursor:pointer;border-color:#00b4ff
}
.small-modal{position:absolute;top:0px;left:0px;width:100%;height:100%;background-color:rgba(0,0,0,0.5);z-index:100
}
.small-modal .form{background-color:white;position:absolute;top:20px;left:20px;padding:20px;border-radius:4px;width:300px
}
.small-modal .form p{margin-bottom:10px
}
.small-modal .form h3{margin-bottom:20px;color:#00b4ff
}
.small-modal .form .close{position:absolute;top:20px;right:20px;width:20px;height:20px;-webkit-mask:url(../../assets/images/add.svg) center center no-repeat;mask:url(../../assets/images/add.svg) center center no-repeat;background-color:#00b4ff;background-size:cover;border:0px;transform:rotateZ(45deg)
}
.small-modal .form .close:hover{cursor:pointer
}
.small-modal .form input{display:block;border:0;font-size:14px
}
.small-modal .form input[type="text"]{height:24px;line-height:24px;border-bottom:3px solid #c6c6c6;width:100%;margin-bottom:30px
}
.small-modal .form input:focus{border-color:#00b4ff
}
.small-modal .form button{display:block
}
.small-modal .form button[type="submit"]{height:34px;border:2px solid #d0d0d0;border-radius:6px;background-color:white;padding:0px 40px;font-weight:bold;margin:0px 0px 0px auto
}
.small-modal .form button[type="submit"]:active{border-color:#00b4ff
}
.small-modal .form ::-webkit-input-placeholder{color:#515151;font-weight:bold
}
.small-modal .form :-ms-input-placeholder{color:#515151;font-weight:bold
}
.small-modal .form ::placeholder{color:#515151;font-weight:bold
}
.small-modal .form ::-moz-placeholder{color:#515151;font-weight:bold
}
.small-modal .form ::-webkit-input-placeholder{color:#515151;font-weight:bold
}



