
*{margin:0px;padding:0px;outline:none
}
body,html,#app{width:100%;height:100%;position:relative
}
input,body,button{font-family:"Open Sans", sans-serif
}
@keyframes bouncy{
0%{transform:translateY(0%)
}
33%{transform:translateY(-100%)
}
66%{transform:translateY(100%)
}
}
@keyframes bounceIn{
from,20%,40%,60%,80%,to{animation-timing-function:cubic-bezier(0.215, 0.61, 0.355, 1)
}
0%{opacity:0;transform:scale3d(0.3, 0.3, 0.3)
}
20%{transform:scale3d(1.1, 1.1, 1.1)
}
40%{transform:scale3d(0.9, 0.9, 0.9)
}
60%{opacity:1;transform:scale3d(1.03, 1.03, 1.03)
}
80%{transform:scale3d(0.97, 0.97, 0.97)
}
to{opacity:1;transform:scale3d(1, 1, 1)
}
}
@keyframes bounceInLeft{
from,60%,75%,90%,to{animation-timing-function:cubic-bezier(0.215, 0.61, 0.355, 1)
}
0%{opacity:0;transform:translate3d(-3000px, 0, 0)
}
60%{opacity:1;transform:translate3d(25px, 0, 0)
}
75%{transform:translate3d(-10px, 0, 0)
}
90%{transform:translate3d(5px, 0, 0)
}
to{transform:none
}
}
.fade-leave-active{transition:opacity 1s
}
.fade-enter,.fade-leave-active{opacity:0
}
.appear-enter-active,.appear-leave-active{transition:opacity .5s
}
.appear-enter,.appear-leave-active{opacity:0
}
.global-loader{position:fixed;top:0px;left:0px;width:100%;height:100%;background-color:rgba(255,255,255,0.9);z-index:5000
}
.global-loader .dots{display:block;font-size:0;color:#333;width:54px;height:18px;position:absolute;top:50%;left:50%;margin-left:-27px;margin-top:-9px
}
.global-loader .dots>div{display:inline-block;width:10px;height:10px;margin:4px;border-radius:100%;animation:bouncy .6s infinite ease-in-out
}
.global-loader .dots>div:nth-child(1){background-color:#0ee1e3;animation-delay:-.14s
}
.global-loader .dots>div:nth-child(2){background-color:#0bd6eb;animation-delay:-.07s
}
.global-loader .dots>div:nth-child(3){background-color:#00b4ff;animation-delay:0s
}
.global-application-error{position:fixed;top:0px;left:0px;width:100%;height:100%;background-color:rgba(255,26,26,0.19);z-index:5000
}
.global-application-error .error{background-color:white;position:absolute;top:50%;left:50%;width:300px;margin-left:-150px;padding:20px;border-radius:6px;text-align:center
}
.global-application-error .error p{margin-bottom:20px
}
.global-application-error .error button{border:2px solid #d0d0d0;height:34px;border-radius:4px;padding:0px 20px;background-color:white
}
.global-application-error .error button:hover{cursor:pointer;border-color:#00b4ff
}
*{margin:0px;padding:0px;outline:none
}
body,html{width:100%;height:100%;background-color:#eaeaea
}
input,body,button{font-family:"Open Sans", sans-serif
}
@keyframes ripple-animation{
from{transform:scale(1);opacity:0.4
}
to{transform:scale(100);opacity:0;display:none
}
}
.ripple{overflow:hidden;position:relative
}
.ripple-effect{position:absolute;border-radius:50%;width:50px;height:50px;background:white;animation:ripple-animation 1s forwards
}
.login-container{width:660px;height:400px;display:-ms-flexbox;display:flex;-ms-flex-direction:row;flex-direction:row;position:absolute;top:50%;left:50%;margin:-200px 0px 0px -330px;border-radius:6px;overflow:hidden;box-shadow:0px 1px 1px 0px rgba(0,0,0,0.33)
}
.login-container .splash{width:400px;background-color:#00b4ff;background-image:url(../assets/images/logo.svg),linear-gradient(135deg, rgba(24,255,209,0.6) 0%, rgba(0,180,255,0.6) 100%);background-position:center center;background-repeat:no-repeat
}
.login-container .form{width:260px;background-color:white;padding:20px;box-sizing:border-box;position:relative
}
.login-container .form .user{width:68px;height:80px;margin:20px auto;background:url(../assets/images/user.svg) center center no-repeat;margin-bottom:40px
}
.login-container .form .error{font-size:12px;font-weight:bold;text-align:center;color:#f6465b;margin-top:10px
}
.login-container .form input{display:block;border:0;font-size:14px
}
.login-container .form input[type="text"],.login-container .form input[type="password"]{height:24px;line-height:24px;border-bottom:3px solid #ebebeb;width:100%;margin-bottom:30px
}
.login-container .form input:focus{border-color:#00b4ff
}
.login-container .form button{display:block
}
.login-container .form button[type="submit"]{height:34px;border:2px solid #ddd;border-radius:6px;background-color:white;padding:0px 40px;font-weight:bold;margin:0px 0px 0px auto
}
.login-container .form button[type="submit"]:active{border-color:#00b4ff
}
.login-container .form ::-webkit-input-placeholder{color:#515151;font-weight:bold
}
.login-container .form :-ms-input-placeholder{color:#515151;font-weight:bold
}
.login-container .form ::placeholder{color:#515151;font-weight:bold
}
.login-container .form ::-moz-placeholder{color:#515151;font-weight:bold
}
.login-container .form ::-webkit-input-placeholder{color:#515151;font-weight:bold
}
.login-container .form footer{color:#acacac;font-weight:bold;text-align:center;position:absolute;left:0px;bottom:20px;width:100%;font-size:12px
}
.login-container .form .working{position:absolute;width:100%;height:100%;top:0px;left:0px;background-color:rgba(0,0,0,0.6)
}
.login-container .form .working .animation,.login-container .form .working .success{width:40px;height:40px;position:absolute;top:50%;left:50%;margin-top:-20px;margin-left:-20px;background-size:contain !important
}
.login-container .form .working .animation{background:url(../assets/images/working.svg) center center no-repeat
}
.login-container .form .working .success{background:url(../assets/images/success.svg) center center no-repeat
}
.login-container .form .working .success.grow{transition:all 500ms ease-in-out
}



