
*{margin:0px;padding:0px;outline:none
}
body,html,#app{width:100%;height:100%;position:relative
}
input,body,button{font-family:"Open Sans", sans-serif
}
@keyframes bouncy{
0%{transform:translateY(0%)
}
33%{transform:translateY(-100%)
}
66%{transform:translateY(100%)
}
}
@keyframes bounceIn{
from,20%,40%,60%,80%,to{animation-timing-function:cubic-bezier(0.215, 0.61, 0.355, 1)
}
0%{opacity:0;transform:scale3d(0.3, 0.3, 0.3)
}
20%{transform:scale3d(1.1, 1.1, 1.1)
}
40%{transform:scale3d(0.9, 0.9, 0.9)
}
60%{opacity:1;transform:scale3d(1.03, 1.03, 1.03)
}
80%{transform:scale3d(0.97, 0.97, 0.97)
}
to{opacity:1;transform:scale3d(1, 1, 1)
}
}
@keyframes bounceInLeft{
from,60%,75%,90%,to{animation-timing-function:cubic-bezier(0.215, 0.61, 0.355, 1)
}
0%{opacity:0;transform:translate3d(-3000px, 0, 0)
}
60%{opacity:1;transform:translate3d(25px, 0, 0)
}
75%{transform:translate3d(-10px, 0, 0)
}
90%{transform:translate3d(5px, 0, 0)
}
to{transform:none
}
}
.fade-leave-active{transition:opacity 1s
}
.fade-enter,.fade-leave-active{opacity:0
}
.appear-enter-active,.appear-leave-active{transition:opacity .5s
}
.appear-enter,.appear-leave-active{opacity:0
}
.global-loader{position:fixed;top:0px;left:0px;width:100%;height:100%;background-color:rgba(255,255,255,0.9);z-index:5000
}
.global-loader .dots{display:block;font-size:0;color:#333;width:54px;height:18px;position:absolute;top:50%;left:50%;margin-left:-27px;margin-top:-9px
}
.global-loader .dots>div{display:inline-block;width:10px;height:10px;margin:4px;border-radius:100%;animation:bouncy .6s infinite ease-in-out
}
.global-loader .dots>div:nth-child(1){background-color:#0ee1e3;animation-delay:-.14s
}
.global-loader .dots>div:nth-child(2){background-color:#0bd6eb;animation-delay:-.07s
}
.global-loader .dots>div:nth-child(3){background-color:#00b4ff;animation-delay:0s
}
.global-application-error{position:fixed;top:0px;left:0px;width:100%;height:100%;background-color:rgba(255,26,26,0.19);z-index:5000
}
.global-application-error .error{background-color:white;position:absolute;top:50%;left:50%;width:300px;margin-left:-150px;padding:20px;border-radius:6px;text-align:center
}
.global-application-error .error p{margin-bottom:20px
}
.global-application-error .error button{border:2px solid #d0d0d0;height:34px;border-radius:4px;padding:0px 20px;background-color:white
}
.global-application-error .error button:hover{cursor:pointer;border-color:#00b4ff
}
.documents-manager{display:-ms-flexbox;display:flex;-ms-flex-direction:row;flex-direction:row;height:100%
}
.documents-manager aside{width:200px;height:100%;background-color:#eee;color:#898989;padding:20px;box-sizing:border-box;position:relative;overflow-x:hidden;-ms-flex-negative:0;flex-shrink:0
}
.documents-manager aside .profile-picture{width:70px;height:70px;background:url(../assets/images/profile-picture.png) center center no-repeat;border-radius:50%;background-size:cover;border:3px solid white;margin:0px auto 20px auto
}
.documents-manager aside .name{text-align:center;font-weight:bold;font-size:14px;color:black;margin-bottom:20px
}
.documents-manager aside .overview{border:1px solid #c6c6c6;border-left:0;border-right:0;font-size:12px;text-align:center;padding:20px 0px;margin-bottom:20px
}
.documents-manager aside .overview span{font-weight:bold;font-size:14px;color:black
}
.documents-manager aside .menu{list-style-type:none;margin-bottom:20px
}
.documents-manager aside .menu li{background:url(../assets/images/arrow.svg) no-repeat left center;padding-left:20px;font-weight:bold;font-size:14px;line-height:20px
}
.documents-manager aside .menu li a{color:black;text-decoration:none
}
.documents-manager aside .sign-out{height:34px;border:2px solid #d0d0d0;border-radius:6px;background-color:transparent;padding:0px 40px;font-weight:bold;margin:0px 0px 0px auto;background:url(../assets/images/sign-out.svg) center center no-repeat;width:100%;cursor:pointer
}
.documents-manager aside .sign-out:active{border-color:#00b4ff
}
.documents-manager aside .sign-out:hover{border-color:#00b4ff;background-image:url(../assets/images/sign-out-focus.svg)
}
.documents-manager aside footer{color:#898989;font-weight:bold;text-align:center;position:absolute;left:0px;bottom:20px;width:100%;font-size:12px
}
.documents-manager .content{height:100%;-ms-flex:1;flex:1;padding:20px;box-sizing:border-box;min-width:450px;background:#fff url(../assets/images/color-logo.svg) right bottom no-repeat;background-position:right 20px bottom 20px;position:relative
}
.documents-manager .content header{border-bottom:1px solid #eee;padding-bottom:20px
}
.documents-manager .content header:after{display:block;content:" ";clear:both
}
.documents-manager .content header .add{display:block;width:80px;height:34px;border:0;background:#00b4ff url(../assets/images/add.svg) center center no-repeat;border-radius:6px;float:left;margin-right:20px
}
.documents-manager .content header .add:hover{cursor:pointer
}
.documents-manager .content header .delete{width:20px;height:34px;border:0;background:transparent url(../assets/images/delete.svg) center center no-repeat;float:left
}
.documents-manager .content header .delete:hover{cursor:pointer
}
.documents-manager .content header .title{float:right;font-size:20px;color:#555;margin-left:20px;line-height:38px
}
.documents-manager .content header .search{width:23px;height:36px;border:0;-webkit-mask:url(../assets/images/search.svg) center center no-repeat;mask:url(../assets/images/search.svg) center center no-repeat;float:right;position:relative;z-index:100
}
.documents-manager .content header .search:hover,.documents-manager .content header .search.active{cursor:pointer;background-color:#00b4ff
}
.documents-manager .content header .search-field{display:block;float:right;border-radius:100px;height:34px;width:0px;padding:0px 0px 0px 36px;margin-left:-30px;transition:width 600ms ease;border:1px solid white
}
.documents-manager .content header .search-field.active{border:1px solid #00b4ff;width:200px
}
.documents-manager .content .documents{padding:20px 0px;box-sizing:border-box;display:-ms-flexbox;display:flex;-ms-flex-direction:row;flex-direction:row;-ms-flex-wrap:wrap;flex-wrap:wrap
}
.documents-manager .content .documents .document{width:100px;padding:5px;padding-top:65px;background:transparent url(../assets/images/document.svg) center 5px no-repeat;margin-right:20px;border:1px solid transparent;border-radius:6px
}
.documents-manager .content .documents .document:hover{border:1px dashed #d0d0d0;cursor:pointer
}
.documents-manager .content .documents .document.selected{border:1px dashed #00b4ff
}
.documents-manager .content .documents .document h4{text-align:center;font-size:11px;margin-bottom:5px;font-weight:bolder
}
.documents-manager .content .documents .document p{text-align:center;font-size:9px;font-weight:bold;color:#898989
}



